const asyncHandler = require('express-async-handler');
const { ContractService } = require('../services/ContractService');

class ContractController {
  /**
   * Listar contratos com paginação e filtros
   */
  static getContracts = asyncHandler(async (req, res) => {
    const page = parseInt(req.query.page) || 1;
    const limit = parseInt(req.query.limit) || 10;
    const search = req.query.search || '';
    const status = req.query.status || '';
    const clientId = req.query.client_id || '';
    const sortBy = req.query.sortBy || 'created_at';
    const sortOrder = req.query.sortOrder || 'desc';

    const result = await ContractService.getContracts({
      page,
      limit,
      search,
      status,
      clientId,
      sortBy,
      sortOrder
    });

    res.json(result);
  });

  /**
   * Obter contrato por ID
   */
  static getContract = asyncHandler(async (req, res) => {
    const { id } = req.params;
    
    const contract = await ContractService.getContractById(id);
    
    if (!contract) {
      return res.status(404).json({ error: 'Contrato não encontrado' });
    }

    res.json(contract);
  });

  /**
   * Criar novo contrato
   */
  static createContract = asyncHandler(async (req, res) => {
    const contractData = req.body;
    
    const contract = await ContractService.createContract(contractData);
    
    res.status(201).json(contract);
  });

  /**
   * Atualizar contrato
   */
  static updateContract = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const updateData = req.body;
    
    const contract = await ContractService.updateContract(id, updateData);
    
    if (!contract) {
      return res.status(404).json({ error: 'Contrato não encontrado' });
    }

    res.json(contract);
  });

  /**
   * Deletar contrato
   */
  static deleteContract = asyncHandler(async (req, res) => {
    const { id } = req.params;
    
    const success = await ContractService.deleteContract(id);
    
    if (!success) {
      return res.status(404).json({ error: 'Contrato não encontrado' });
    }

    res.json({ message: 'Contrato deletado com sucesso' });
  });

  /**
   * Obter pagamentos do contrato
   */
  static getContractPayments = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const page = parseInt(req.query.page) || 1;
    const limit = parseInt(req.query.limit) || 10;
    const status = req.query.status || '';
    
    const result = await ContractService.getContractPayments(id, {
      page,
      limit,
      status
    });

    res.json(result);
  });

  /**
   * Gerar parcelas do contrato
   */
  static generateInstallments = asyncHandler(async (req, res) => {
    const { id } = req.params;
    
    const installments = await ContractService.generateInstallments(id);
    
    res.json(installments);
  });

  /**
   * Calcular estatísticas do contrato
   */
  static getContractStats = asyncHandler(async (req, res) => {
    const { id } = req.params;
    
    const stats = await ContractService.getContractStats(id);
    
    res.json(stats);
  });

  /**
   * Buscar contratos por termo
   */
  static searchContracts = asyncHandler(async (req, res) => {
    const { query } = req.params;
    const limit = parseInt(req.query.limit) || 10;
    
    const contracts = await ContractService.searchContracts(query, limit);
    
    res.json(contracts);
  });

  /**
   * Atualizar status do contrato
   */
  static updateContractStatus = asyncHandler(async (req, res) => {
    const { id } = req.params;
    const { status } = req.body;
    
    const contract = await ContractService.updateContractStatus(id, status);
    
    if (!contract) {
      return res.status(404).json({ error: 'Contrato não encontrado' });
    }

    res.json(contract);
  });
}

module.exports = { ContractController };